import React from "react";
import Particles from "react-tsparticles";
import {loadFull} from "tsparticles";
import { tsParticles } from "@tsparticles/engine";
import { loadHeartShape } from "@tsparticles/shape-heart";
import { useEffect } from "react";
import { Carousel } from "@trendyol-js/react-carousel";
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import OurUni from './images/slides/OurUniverse.webp'
import AmogusUni from './images/slides/AmogusUniverse.webp'
import MinecraftUni from './images/slides/MinecraftUniverse.webp'
import PillsUni from './images/slides/PillsUniverse.webp'
import PokeUni from './images/slides/PokemonUniverse.webp'
import RockUni from './images/slides/RockUniverse.webp'
import SolarUni from './images/slides/SolarUniverse.webp'
import ToasterUni from './images/slides/ToasterUniverse.webp'
import WormUni from './images/slides/WormUniverse.webp'
import NinUni from './images/slides/YoshiUniverse.webp'

function ParticleBg(){
  var animationDuration = 1;

  var settings = {
		infinite: true,
		speed: 500,
		slidesToShow: 1,
		slidesToScroll: 1,
    
	  };
  const Universes = [
    {
      name:'Would we fall in love in another universe?',
      img:OurUni
    },
    {
      name:'Blocky Yet Cozy',
      img:MinecraftUni
    },
    {
      name:'Ejected But United',
      img:AmogusUni
    },
    {
      name:'Mushy Sleepers',
      img:WormUni
    },
    {
      name:'Two Pebbles and The World',
      img:RockUni
    },
    {
      name:'Solar Eclipse',
      img:SolarUni
    },
    {
      name:'Toaster Bath',
      img:ToasterUni
    },
    {
      name:'Out of the Bottle',
      img:PillsUni
    },
    {
      name:'Poke-Partners',
      img:PokeUni
    },
    {
      name:'Nintendo Partners',
      img:NinUni
    }
    
  ]
 
    async function loadParticles(main){
      //await loadHeartShape(tsParticles);
        await loadFull(main);
    }
    const containerRef = React.useRef();

    React.useEffect(() => {
      if (containerRef && containerRef.current) {
        console.log(containerRef.current);
      }
    });
    const pauseAnimation = () => {
      // containerRef.current.plugins.get("emitters").array[0].pause();
      // containerRef.current.plugins.get("emitters").array[1].pause();
    }
  const removeWindow = () => {
    document.getElementById('Overlay').style.display = 'none';
    document.getElementById('Content').style.filter = 'blur(0px)';
    document.getElementById('Content').style.backgroundColor = 'rgba(0,0,0,0.0)';
    document.querySelector('.BEEGApproveButton').style.display = 'none';
    containerRef.current.start();
    containerRef.current.play();
    containerRef.current.plugins.get("emitters").array[0].play();
    containerRef.current.plugins.get("emitters").array[1].play();
    
    console.log(containerRef.current)
  }

  const speedUp = () => {
    if(animationDuration> 0.0078125){
      animationDuration = animationDuration/2;
      console.log(animationDuration)
      document.getElementById('Overlay').style.animationDuration = animationDuration+'s';
      return
    }
    document.getElementById('Overlay').style.animationDuration = '';
    document.querySelector('.BEEGApproveButton').style.display = 'block';
  }
    
    return(
        <section>
          <Particles
        id="tsparticles"
        init={loadParticles}
        container={containerRef}
        loaded={pauseAnimation}
        options={{
          "autoPlay": false,
          "background": {
            "color": {
              "value": "#FFE3ED"
            },
            "image": "",
            "position": "",
            "repeat": "",
            "size": "",
            "opacity": 1
          },
          "backgroundMask": {
            "composite": "destination-out",
            "cover": {
              "color": {
                "value": "#fff"
              },
              "opacity": 1
            },
            "enable": false
          },
          "clear": true,
          "defaultThemes": {},
          "delay": 0,
          "fullScreen": {
            "enable": true,
            "zIndex": -1
          },
          "detectRetina": true,
          "duration": 0,
          "fpsLimit": 120,
          "interactivity": {
            "detectsOn": "window",
            "events": {
              "onClick": {
                "enable": false,
                "mode": []
              },
              "onDiv": {
                "selectors": [],
                "enable": false,
                "mode": [],
                "type": "circle"
              },
              "onHover": {
                "enable": false,
                "mode": [],
                "parallax": {
                  "enable": false,
                  "force": 2,
                  "smooth": 10
                }
              },
              "resize": {
                "delay": 0.5,
                "enable": true
              }
            },
            "modes": {
              "trail": {
                "delay": 1,
                "pauseOnStop": false,
                "quantity": 1
              },
              "attract": {
                "distance": 200,
                "duration": 0.4,
                "easing": "ease-out-quad",
                "factor": 1,
                "maxSpeed": 50,
                "speed": 1
              },
              "bounce": {
                "distance": 200
              },
              "bubble": {
                "distance": 200,
                "duration": 0.4,
                "mix": false,
                "divs": {
                  "distance": 200,
                  "duration": 0.4,
                  "mix": false,
                  "selectors": []
                }
              },
              "connect": {
                "distance": 80,
                "links": {
                  "opacity": 0.5
                },
                "radius": 60
              },
              "grab": {
                "distance": 100,
                "links": {
                  "blink": false,
                  "consent": false,
                  "opacity": 1
                }
              },
              "push": {
                "default": true,
                "groups": [],
                "quantity": 4
              },
              "remove": {
                "quantity": 2
              },
              "repulse": {
                "distance": 200,
                "duration": 0.4,
                "factor": 100,
                "speed": 1,
                "maxSpeed": 50,
                "easing": "ease-out-quad",
                "divs": {
                  "distance": 200,
                  "duration": 0.4,
                  "factor": 100,
                  "speed": 1,
                  "maxSpeed": 50,
                  "easing": "ease-out-quad",
                  "selectors": []
                }
              },
              "slow": {
                "factor": 3,
                "radius": 200
              },
              "light": {
                "area": {
                  "gradient": {
                    "start": {
                      "value": "#ffffff"
                    },
                    "stop": {
                      "value": "#000000"
                    }
                  },
                  "radius": 1000
                },
                "shadow": {
                  "color": {
                    "value": "#000000"
                  },
                  "length": 2000
                }
              }
            }
          },
          "manualParticles": [],
          "particles": {
            "bounce": {
              "horizontal": {
                "value": 1
              },
              "vertical": {
                "value": 1
              }
            },
            "collisions": {
              "absorb": {
                "speed": 2
              },
              "bounce": {
                "horizontal": {
                  "value": 1
                },
                "vertical": {
                  "value": 1
                }
              },
              "enable": false,
              "maxSpeed": 50,
              "mode": "bounce",
              "overlap": {
                "enable": true,
                "retries": 0
              }
            },
            "color": {
              "value": "#ffd700",
              "animation": {
                "h": {
                  "count": 0,
                  "enable": false,
                  "speed": 1,
                  "decay": 0,
                  "delay": 0,
                  "sync": true,
                  "offset": 0
                },
                "s": {
                  "count": 0,
                  "enable": false,
                  "speed": 1,
                  "decay": 0,
                  "delay": 0,
                  "sync": true,
                  "offset": 0
                },
                "l": {
                  "count": 0,
                  "enable": false,
                  "speed": 1,
                  "decay": 0,
                  "delay": 0,
                  "sync": true,
                  "offset": 0
                }
              }
            },
            "effect": {
              "close": true,
              "fill": true,
              "options": {},
              "type": []
            },
            "groups": {},
            "move": {
              "angle": {
                "offset": 0,
                "value": 90
              },
              "attract": {
                "distance": 200,
                "enable": false,
                "rotate": {
                  "x": 3000,
                  "y": 3000
                }
              },
              "center": {
                "x": 50,
                "y": 50,
                "mode": "percent",
                "radius": 0
              },
              "decay": 0.05,
              "distance": {},
              "direction": "top",
              "drift": 0,
              "enable": true,
              "gravity": {
                "acceleration": 9.81,
                "enable": true,
                "inverse": false,
                "maxSpeed": 50
              },
              "path": {
                "clamp": true,
                "delay": {
                  "value": 0
                },
                "enable": false,
                "options": {}
              },
              "outModes": {
                "default": "destroy",
                "bottom": "destroy",
                "left": "destroy",
                "right": "destroy",
                "top": "none"
              },
              "random": false,
              "size": false,
              "speed": {
                "min": 10,
                "max": 50
              },
              "spin": {
                "acceleration": 0,
                "enable": false
              },
              "straight": false,
              "trail": {
                "enable": false,
                "length": 10,
                "fill": {}
              },
              "vibrate": false,
              "warp": false
            },
            "number": {
              "density": {
                "enable": false,
                "width": 1920,
                "height": 1080
              },
              "limit": {
                "mode": "delete",
                "value": 0
              },
              "value": 0
            },
            "opacity": {
              "value": 1,
              "animation": {
                "count": 0,
                "enable": false,
                "speed": 2,
                "decay": 0,
                "delay": 0,
                "sync": false,
                "mode": "auto",
                "startValue": "random",
                "destroy": "none"
              }
            },
            "reduceDuplicates": false,
            "shadow": {
              "blur": 0,
              "color": {
                "value": "#000"
              },
              "enable": false,
              "offset": {
                "x": 0,
                "y": 0
              }
            },
            "shape": {
              "close": true,
              "fill": true,
              "options": {
                "image": [
                  {
                    "src": "https://upload.wikimedia.org/wikipedia/commons/thumb/f/f1/Heart_coraz%C3%B3n.svg/1200px-Heart_coraz%C3%B3n.svg.png",
                    "width": 32,
                    "height": 32,
                    "particles": {
                      "size": {
                        "value": 16
                      }
                    }
                  },
                  {
                    "src": "https://creazilla-store.fra1.digitaloceanspaces.com/cliparts/77965/pink-heart-clipart-sm.png",
                    "width": 32,
                    "height": 32,
                    "particles": {
                      "size": {
                        "value": 16
                      }
                    }
                  }]
              },
              "type": "image"
            },
            "size": {
              "value": 14,
              "animation": {
                "count": 1,
                "enable": true,
                "speed": 16,
                "decay": 0,
                "delay": 0,
                "sync": true,
                "mode": "auto",
                "startValue": "min",
                "destroy": "none"
              }
            },
            "stroke": {
              "width": 0,
              "color": {
                "value": "",
                "animation": {
                  "h": {
                    "count": 0,
                    "enable": false,
                    "speed": 0,
                    "decay": 0,
                    "delay": 0,
                    "sync": false,
                    "offset": 0
                  },
                  "s": {
                    "count": 0,
                    "enable": false,
                    "speed": 1,
                    "decay": 0,
                    "delay": 0,
                    "sync": true,
                    "offset": 0
                  },
                  "l": {
                    "count": 0,
                    "enable": false,
                    "speed": 1,
                    "decay": 0,
                    "delay": 0,
                    "sync": true,
                    "offset": 0
                  }
                }
              }
            },
            "zIndex": {
              "value": 0,
              "opacityRate": 1,
              "sizeRate": 1,
              "velocityRate": 1
            },
            "destroy": {
              "bounds": {},
              "mode": "none",
              "split": {
                "count": 1,
                "factor": {
                  "value": 3
                },
                "rate": {
                  "value": {
                    "min": 4,
                    "max": 9
                  }
                },
                "sizeOffset": true,
                "particles": {}
              }
            },
            "roll": {
              "darken": {
                "enable": true,
                "value": 25
              },
              "enable": true,
              "enlighten": {
                "enable": false,
                "value": 0
              },
              "mode": "vertical",
              "speed": {
                "min": 5,
                "max": 15
              }
            },
            "tilt": {
              "value": {
                "min": 0,
                "max": 360
              },
              "animation": {
                "enable": true,
                "speed": 30,
                "decay": 0,
                "sync": false
              },
              "direction": "random",
              "enable": true
            },
            "twinkle": {
              "lines": {
                "enable": false,
                "frequency": 0.05,
                "opacity": 1
              },
              "particles": {
                "enable": false,
                "frequency": 0.05,
                "opacity": 1
              }
            },
            "wobble": {
              "distance": 30,
              "enable": true,
              "speed": {
                "angle": {
                  "min": -7,
                  "max": 7
                },
                "move": 10
              }
            },
            "life": {
              "count": 0,
              "delay": {
                "value": 0,
                "sync": false
              },
              "duration": {
                "value": 0,
                "sync": false
              }
            },
            "rotate": {
              "value": {
                "min": 0,
                "max": 360
              },
              "animation": {
                "enable": true,
                "speed": 30,
                "decay": 0,
                "sync": false
              },
              "direction": "random",
              "path": false
            },
            "orbit": {
              "animation": {
                "count": 0,
                "enable": false,
                "speed": 1,
                "decay": 0,
                "delay": 0,
                "sync": false
              },
              "enable": false,
              "opacity": 1,
              "rotation": {
                "value": 45
              },
              "width": 1
            },
            "links": {
              "blink": false,
              "color": {
                "value": "#fff"
              },
              "consent": false,
              "distance": 100,
              "enable": false,
              "frequency": 1,
              "opacity": 1,
              "shadow": {
                "blur": 5,
                "color": {
                  "value": "#000"
                },
                "enable": false
              },
              "triangles": {
                "enable": false,
                "frequency": 1
              },
              "width": 1,
              "warp": false
            },
            "repulse": {
              "value": 0,
              "enabled": false,
              "distance": 1,
              "duration": 1,
              "factor": 1,
              "speed": 1
            }
          },
          "pauseOnBlur": true,
          "pauseOnOutsideViewport": true,
          "responsive": [],
          "smooth": false,
          "style": {},
          "themes": [],
          "zLayers": 100,
          "emitters": [
            {
              "autoPlay": true,
              "fill": true,
              "life": {
                "wait": false
              },
              "rate": {
                "quantity": 5,
                "delay": 0.15
              },
              "shape": {
                "options": {},
                "replace": {
                  "color": false,
                  "opacity": false
                },
                "type": "square"
              },
              "startCount": 0,
              "particles": {
                "move": {
                  "direction": "top-right",
                  "outModes": {
                    "top": "none",
                    "left": "none",
                    "default": "destroy"
                  }
                }
              },
              "position": {
                "x": 0,
                "y": 30
              }
            },
            {
              "autoPlay": true,
              "fill": true,
              "life": {
                "wait": false
              },
              "rate": {
                "quantity": 5,
                "delay": 0.15
              },
              "shape": {
                "options": {},
                "replace": {
                  "color": false,
                  "opacity": false
                },
                "type": "square"
              },
              "startCount": 0,
              "particles": {
                "move": {
                  "direction": "top-left",
                  "outModes": {
                    "top": "none",
                    "right": "none",
                    "default": "destroy"
                  }
                }
              },
              "position": {
                "x": 100,
                "y": 30
              }
            }
          ],
          "motion": {
            "disable": false,
            "reduce": {
              "factor": 4,
              "value": true
            }
          }
        }}

        />

<div className="App">
      <section id="Content">
        <section className="Information">
          <h1 className="HeadingTitle">HAPPY VALENTINES DAY MI AMOR</h1>
          <p>Thank you very much for another year of happy memories and another year for future memories. You've done so much for me and I can't thank you enough for everything that you do for me. No matter the universe, I know that you'll always be there for me as much as I am there for you. Here's to us and a special Valentine's Day &#10084;</p>

         
          <Slider className='Slider'  {...settings} >
          {Universes.map( (universe, index) => (
						<section className='Universe' key={index} >
							<img className="UniverseImg" src={universe.img} alt='Universe here' />

								<p>{universe.name}</p>
								
						</section>
					))}
          </Slider>
        </section>
      </section>
        <section id='Overlay'>
          <h1>Will you be My Valentine?</h1>
          <img style={{borderRadius:'20px',boxShadow:'0 0 10px black'}} srcSet={require("./images/additional/CatEyes.webp")} alt='Maou' /> <br />
          <button className='ApproveButton' onClick={removeWindow}>Yes</button> 
          <button className='DenyButton' onClick={speedUp}>No</button>
        </section>
        <button className='BEEGApproveButton' onClick={removeWindow}>Yes</button> 
    </div>

        </section>
    )
}

export default ParticleBg